import React, { useState, useRef, useEffect } from 'react';
import { Play, Pause, Volume2, VolumeX } from 'lucide-react';

const AudioPlayer = ({ track, onPlay, isCurrentTrack, isPlaying }) => {
  const audioRef = useRef(null);
  const [volume, setVolume] = useState(0.7);
  const [isMuted, setIsMuted] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);

  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;

    const updateTime = () => setCurrentTime(audio.currentTime);
    const updateDuration = () => setDuration(audio.duration);

    audio.addEventListener('timeupdate', updateTime);
    audio.addEventListener('loadedmetadata', updateDuration);

    return () => {
      audio.removeEventListener('timeupdate', updateTime);
      audio.removeEventListener('loadedmetadata', updateDuration);
    };
  }, []);

  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;

    if (isCurrentTrack && isPlaying) {
      audio.play().catch(console.error);
    } else {
      audio.pause();
    }
  }, [isCurrentTrack, isPlaying]);

  useEffect(() => {
    const audio = audioRef.current;
    if (audio) {
      audio.volume = isMuted ? 0 : volume;
    }
  }, [volume, isMuted]);

  const handlePlayPause = () => {
    if (track.preview_url) {
      onPlay(track);
    }
  };

  const handleVolumeChange = (e) => {
    const newVolume = parseFloat(e.target.value);
    setVolume(newVolume);
    setIsMuted(newVolume === 0);
  };

  const toggleMute = () => {
    setIsMuted(!isMuted);
  };

  const formatTime = (time) => {
    if (isNaN(time)) return '0:00';
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  if (!track.preview_url) {
    return (
      <div className="flex items-center gap-2 opacity-50">
        <div className="w-8 h-8 rounded-full flex items-center justify-center" style={{
          background: 'var(--border-color)'
        }}>
          <Play size={14} style={{ color: 'var(--text-muted)' }} />
        </div>
        <span className="text-xs" style={{ color: 'var(--text-muted)' }}>
          No preview
        </span>
      </div>
    );
  }

  return (
    <div className="flex items-center gap-3">
      <audio
        ref={audioRef}
        src={track.preview_url}
        preload="metadata"
      />
      
      <button
        onClick={handlePlayPause}
        className="w-8 h-8 rounded-full flex items-center justify-center transition-all hover:scale-110"
        style={{
          background: isCurrentTrack && isPlaying 
            ? 'var(--accent-gradient)' 
            : 'var(--border-color)'
        }}
      >
        {isCurrentTrack && isPlaying ? (
          <Pause size={14} style={{ color: '#fff' }} />
        ) : (
          <Play size={14} style={{ color: 'var(--text-primary)' }} />
        )}
      </button>

      {isCurrentTrack && (
        <div className="flex items-center gap-2">
          <span className="text-xs" style={{ color: 'var(--text-muted)' }}>
            {formatTime(currentTime)}
          </span>
          
          <div className="flex items-center gap-1">
            <button onClick={toggleMute} className="p-1">
              {isMuted ? (
                <VolumeX size={14} style={{ color: 'var(--text-muted)' }} />
              ) : (
                <Volume2 size={14} style={{ color: 'var(--text-muted)' }} />
              )}
            </button>
            
            <input
              type="range"
              min="0"
              max="1"
              step="0.05"
              value={isMuted ? 0 : volume}
              onChange={handleVolumeChange}
              className="w-16 h-1 bg-gray-600 rounded-lg appearance-none cursor-pointer"
              style={{
                background: `linear-gradient(to right, var(--accent-color) 0%, var(--accent-color) ${volume * 100}%, var(--border-color) ${volume * 100}%, var(--border-color) 100%)`
              }}
            />
          </div>
          
          <span className="text-xs" style={{ color: 'var(--text-muted)' }}>
            {formatTime(duration)}
          </span>
        </div>
      )}
    </div>
  );
};

export default AudioPlayer;