import React, { useState } from 'react';
import { Sparkles, Music2, Play } from 'lucide-react';
import api from '../utils/api';
import AudioPlayer from '../components/UI/AudioPlayer';

const PlaylistGenerator = () => {
  const [moodInput, setMoodInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [playlists, setPlaylists] = useState([]);
  const [error, setError] = useState('');
  const [currentTrack, setCurrentTrack] = useState(null);
  const [isPlaying, setIsPlaying] = useState(false);

  const quickMoods = [
    "Happy & Energetic",
    "Chill & Relaxed", 
    "Workout Mode",
    "Study Focus",
    "Romantic Evening",
    "Road Trip",
    "Melancholy",
    "Party Time"
  ];

  const generatePlaylist = async (mood = null) => {
    const targetMood = mood || moodInput;
    
    if (!targetMood.trim()) {
      setError('Please describe your mood or what type of music you want!');
      return;
    }

    setIsLoading(true);
    setError('');
    setPlaylists([]);

    try {
      console.log('ðŸŽµ Generating playlist for mood:', targetMood);
      
      const aiResponse = await api.post('/gemini/analyze-mood', {
        moodDescription: targetMood,
        genre: 'mixed',
        count: 20
      });

      console.log('ðŸ¤– AI Analysis:', aiResponse.data);

      if (aiResponse.data.tracks && aiResponse.data.tracks.length > 0) {
        const spotifyPromises = aiResponse.data.tracks.map(async (track) => {
          try {
            const spotifyResponse = await api.post('/spotify/search', {
              query: `${track.title} ${track.artist}`,
              limit: 1
            });
            
            if (spotifyResponse.data.tracks && spotifyResponse.data.tracks.length > 0) {
              return {
                ...spotifyResponse.data.tracks[0],
                aiReason: track.reason || 'Perfect for your mood'
              };
            }
            return null;
          } catch (error) {
            console.warn('Failed to find track on Spotify:', track, error);
            return null;
          }
        });

        const spotifyTracks = (await Promise.all(spotifyPromises)).filter(Boolean);
        
        if (spotifyTracks.length > 0) {
          const newPlaylist = {
            name: aiResponse.data.playlistName || `${targetMood} Playlist`,
            description: aiResponse.data.description || `A personalized playlist for your ${targetMood} mood`,
            tracks: spotifyTracks,
            mood: targetMood,
            createdAt: new Date(),
            totalTracks: spotifyTracks.length,
            duration: spotifyTracks.reduce((total, track) => total + (track.duration_ms || 0), 0)
          };

          setPlaylists([newPlaylist]);
          console.log('âœ… Playlist generated successfully:', newPlaylist);
        } else {
          throw new Error('No tracks found on Spotify for the AI recommendations');
        }
      } else {
        throw new Error('AI could not generate recommendations for this mood');
      }

    } catch (error) {
      console.error('âŒ Playlist generation failed:', error);
      
      let errorMessage = 'Failed to generate playlist. ';
      
      if (error.response?.status === 401) {
        errorMessage += 'Authentication failed. Please sign in again.';
      } else if (error.response?.status === 429) {
        errorMessage += 'Too many requests. Please wait a moment and try again.';
      } else if (error.message.includes('AI could not generate')) {
        errorMessage += 'Try describing your mood differently or be more specific.';
      } else if (error.message.includes('No tracks found')) {
        errorMessage += 'Try a different mood or more common music preferences.';
      } else {
        errorMessage += error.message || 'Please check your connection and try again.';
      }
      
      setError(errorMessage);
      
      console.log('ðŸ”„ Falling back to demo data...');
      const mockTracks = [
        { name: "Good as Hell", artist: "Lizzo", album: "Cuz I Love You", duration_ms: 219000 },
        { name: "Uptown Funk", artist: "Mark Ronson ft. Bruno Mars", album: "Uptown Special", duration_ms: 270000 },
        { name: "Can't Stop the Feeling!", artist: "Justin Timberlake", album: "Trolls Soundtrack", duration_ms: 236000 },
        { name: "Happy", artist: "Pharrell Williams", album: "G I R L", duration_ms: 232000 },
        { name: "Walking on Sunshine", artist: "Katrina and the Waves", album: "Walking on Sunshine", duration_ms: 239000 }
      ];
      
      setPlaylists([{
        name: `${targetMood} Vibes`,
        description: `A personalized playlist for your ${targetMood} mood`,
        mood: targetMood,
        tracks: mockTracks,
        createdAt: new Date(),
        totalTracks: mockTracks.length,
        duration: mockTracks.reduce((total, track) => total + track.duration_ms, 0)
      }]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    generatePlaylist();
  };

  const formatDuration = (ms) => {
    const minutes = Math.floor(ms / 60000);
    const seconds = Math.floor((ms % 60000) / 1000);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  const handlePlayTrack = (track) => {
    if (currentTrack && currentTrack.spotifyId === track.spotifyId && isPlaying) {
      setIsPlaying(false);
    } else {
      setCurrentTrack(track);
      setIsPlaying(true);
    }
  };

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-12">
        <div className="flex items-center justify-center gap-3 mb-6">
          <div style={{
            background: 'var(--accent-gradient)',
            padding: '12px',
            borderRadius: '50%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }}>
            <Sparkles size={28} style={{ color: '#fff' }} />
          </div>
          <h1 className="text-4xl font-bold" style={{ color: 'var(--text-primary)' }}>
            AI Playlist Generator
          </h1>
        </div>
        <p className="text-xl" style={{ color: 'var(--text-secondary)' }}>
          Describe your mood and let AI create the perfect playlist for you
        </p>
      </div>

      <div className="glass-card p-8 rounded-3xl mb-8">
        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label className="block text-lg font-semibold mb-3" style={{ color: 'var(--text-primary)' }}>
              How are you feeling? What's your vibe?
            </label>
            <textarea
              value={moodInput}
              onChange={(e) => setMoodInput(e.target.value)}
              placeholder="E.g., I'm feeling nostalgic and want something mellow, or I need high-energy music for my workout..."
              className="w-full p-4 rounded-2xl border-2 resize-none focus:outline-none focus:ring-2 focus:ring-blue-500"
              style={{
                background: 'var(--input-bg)',
                color: 'var(--text-primary)',
                borderColor: 'var(--border-color)',
                minHeight: '120px'
              }}
              disabled={isLoading}
            />
          </div>

          <button
            type="submit"
            disabled={isLoading || !moodInput.trim()}
            className="btn btn-primary w-full py-4 text-lg font-semibold rounded-2xl transition-all flex items-center justify-center gap-3 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isLoading ? (
              <>
                <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                Generating Your Perfect Playlist...
              </>
            ) : (
              <>
                <Sparkles size={20} />
                Generate Playlist
              </>
            )}
          </button>
        </form>

        <div className="mt-8">
          <p className="text-center mb-4" style={{ color: 'var(--text-secondary)' }}>
            Or try one of these moods:
          </p>
          <div className="flex flex-wrap gap-3 justify-center">
            {quickMoods.map((mood) => (
              <button
                key={mood}
                onClick={() => generatePlaylist(mood)}
                disabled={isLoading}
                className="px-4 py-2 rounded-full border-2 transition-all hover:scale-105 disabled:opacity-50"
                style={{
                  background: 'var(--bg-secondary)',
                  color: 'var(--text-primary)',
                  borderColor: 'var(--border-color)'
                }}
              >
                {mood}
              </button>
            ))}
          </div>
        </div>
      </div>

      {error && (
        <div className="glass-card p-6 rounded-2xl mb-6 border-l-4 border-red-500" style={{
          background: 'rgba(239, 68, 68, 0.1)'
        }}>
          <div className="flex items-center gap-3">
            <i className="fas fa-exclamation-triangle text-red-400 text-xl"></i>
            <div>
              <h3 className="font-semibold text-red-300">Oops! Something went wrong</h3>
              <p className="text-red-200 mt-1">{error}</p>
            </div>
          </div>
        </div>
      )}

      {playlists.map((playlist, index) => (
        <div key={index} className="glass-card p-8 rounded-3xl">
          <div className="flex items-start justify-between mb-6">
            <div>
              <h2 className="text-2xl font-bold mb-2" style={{ color: 'var(--text-primary)' }}>
                {playlist.name}
              </h2>
              <p className="mb-2" style={{ color: 'var(--text-secondary)' }}>
                {playlist.description}
              </p>
              <div className="flex items-center gap-4 text-sm" style={{ color: 'var(--text-muted)' }}>
                <span>{playlist.totalTracks} tracks</span>
                <span>â€¢</span>
                <span>{Math.round(playlist.duration / 60000)} minutes</span>
              </div>
            </div>
            <button className="btn btn-outline px-6 py-2 rounded-full flex items-center gap-2">
              <Play size={16} />
              Save Playlist
            </button>
          </div>

          <div className="space-y-3">
            {playlist.tracks.map((track, trackIndex) => (
              <div key={trackIndex} className="flex items-center gap-4 p-3 rounded-xl hover:bg-white hover:bg-opacity-5 transition-all">
                <div className="w-8 h-8 rounded-full flex items-center justify-center" style={{
                  background: 'var(--accent-gradient)',
                  color: '#fff',
                  fontSize: '0.8rem',
                  fontWeight: 'bold'
                }}>
                  {trackIndex + 1}
                </div>
                <Music2 size={20} style={{ color: 'var(--text-muted)' }} />
                <div className="flex-1">
                  <div className="font-medium" style={{ color: 'var(--text-primary)' }}>
                    {track.name}
                  </div>
                  <div className="text-sm" style={{ color: 'var(--text-secondary)' }}>
                    {track.artist} â€¢ {track.album}
                  </div>
                  {track.aiReason && (
                    <div className="text-xs mt-1 italic" style={{ color: 'var(--text-muted)' }}>
                      {track.aiReason}
                    </div>
                  )}
                </div>
                <div className="flex items-center gap-3">
                  <AudioPlayer
                    track={track}
                    onPlay={handlePlayTrack}
                    isCurrentTrack={currentTrack && currentTrack.spotifyId === track.spotifyId}
                    isPlaying={isPlaying}
                  />
                  <div className="text-sm" style={{ color: 'var(--text-muted)' }}>
                    {track.duration_ms ? formatDuration(track.duration_ms) : '3:30'}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default PlaylistGenerator;